import React from 'react';

const CartCard=(props)=>{
    return(
    <div className="card" style={{width: "100%", marginTop:"2vh"}}>
        <div className="card-body">
            <div className="row">
                <div className="col">
                    <h5 className="card-title">{props.data.title}</h5>
                    <p className="card-text"><b>{props.data.price}</b></p>
                    <p className="card-text">Ket: {props.ket}</p>
                </div>
                <div className="col-lg-3" style={{position:"relative"}}>
                    <button className="btn btn-danger" onClick={props.rmCart} style={{position:"absolute", right:"2vw", bottom: 0}} >Remove</button>
                </div>
            </div>
        </div>
    </div>   
    )
}

export default CartCard;