import React from 'react';
import {connect} from 'react-redux';
import CartCard from './components/CartCard';
import {rmCart} from './redux/action';

const Pembayaran=(props)=>{
    const getWaLink=()=>{
        let totalrp=0;
        let totald=0;
        let message=`Order ${props.cart.length} items:`;
        props.cart.forEach((item,i)=>{
            totalrp=totalrp+parseInt(item.price.substr(3,4))
            totald=totald+parseFloat(item.price[13] ==="$" ? item.price[12] : item.price.substr(12,13))
            message=message+`

-${item.title}
Price: ${item.price}
Info: ${props.ket[i]}`});
        message=message+`

Total payment: ${totalrp.toString()}.000 / ${totald.toString()}$`;
        message=encodeURI(message);
        return `https://web.whatsapp.com/send?phone=62895701288405&text=${message}&app_absent=0`
    }
    return(
        <>
        <div className="container-fluid mt-3">
            <div className="row justify-content-center"><h1>HALAMAN PEMBAYARAN</h1></div>
            <div className="row justify-content-center">{props.cart.length > 0 ? "":<h3>0</h3>}</div></div>
        <div className="container">
        { props.cart.length > 0 ? props.cart.map((item, i)=><CartCard data={item} ket={props.ket[i]} rmCart={()=>props.rmCart(i)} key={i} />) : ""}
        { props.cart.length > 0 ? <div className="container-fluid mt-3"><div className="row justify-content-center"><a href={getWaLink()}><button className="btn btn-success">Order Via Whatsapp</button></a></div></div> : ""}
        </div>
        </>
    )
}

const setProps=(state)=>{
    return {
        cart:state.cart,
        ket:state.ket
    }
}

export default connect(setProps, {rmCart})(Pembayaran);